.header{
    width: 100%;
    position: fixed;
    height: 5%;
    padding: 8px;
    left: 0;
    z-index: var(--z-fixed);
    background-color: rgb(255, 255, 255);
}

.nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 1rem;
    height: calc(var(--header-height)+1.5rem);
}

.nav__logo,
.nav__toggle{
    color: var(--title-color);
    font-weight: var(--font-medium);
}

.nav__list{
    display: flex;
    column-gap: 2rem;
}

.nav__link{
    display: flex;
    flex-direction: column ;
    align-items: center;
    font-size: var(--small-font-size);
    color: var(--title-color);
    font-weight: var(--font-medium);
    transition: .3s;
}

.nav__icon,
.nav__close,
.nav__toggle{
    display: none;
}

.active-link,
.nav__link:hover{
    color: var(--title-color-dark);
}

@media screen and (max-width: 768px) {
    .header {
      top: initial;
      bottom: 0;
    }
  
    .nav {
        height: var(--header-height);
    }

    .nav__menu{
        position: fixed;
        bottom: -100%;
        left: 0;
        width: 100%;
        padding: 2rem 1.5rem;
        background-color: var(--body-color);
        box-shadow: 0 12px 21px rgba(0, 0, 0, 0.55);
        border-radius: 3.5rem 3.5rem 0 0;
        transition: .3s;
    }

    .show-menu{
        bottom: 0;
    }

    .nav__list{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 2rem;
    }

    .nav__icon{
        font-size: 1.2rem;
    }

    .nav__close{
        position: relative;
        left: 95%;
        font-size: 1.5rem;
        cursor: pointer;
    }

    .nav__toggle{
        font-size: 1.5rem;
        cursor: pointer;
    }

  

    .nav__icon,
    .nav__close,
    .nav__toggle{
        display: block;
    }
  }

  @media screen and (max-width: 350px) {
    .nav__menu{
        padding: 2rem 0.25rem 4rem;
    }

    .nav__list{
        column-gap: 0;
    }
  }